<template>
  <div class="mains">
    <div class="left">
      <div>
        <h2>超级企业列表({{ length }})</h2>
        <a-input
          placeholder="请输入企业名称"
          allow-clear
          @change="onChange"
          style="width: 60%"
          v-model="search"
          @input="Searchs"
        />
        <a-button type="primary" style="margin-left: 10px" @click="add"
          ><a-icon type="plus"
        /></a-button>
        <div class="addEnter" v-show="Add">
          <h2>
            添加企业<a-button type="link" @click="add">
              <a-icon type="close" />
            </a-button>
          </h2>
          <div>
            <ul>
              <li>
                <span>企业名称:</span>
                <a-input placeholder="企业名称" v-model="name" />
              </li>
              <li>
                <span>企业域名:</span>
                <a-input placeholder="企业域名" v-model="ip" />
              </li>
              <li>
                <span>超级管理员账号:</span>
                <a-input placeholder="超级管理员账号" v-model="admin" />
              </li>
              <li>
                <span>超级管理员邮箱:</span>
                <a-input placeholder="超级管理员邮箱" v-model="email" />
              </li>
              <li>
                <span>最大坐席数:</span>
                <a-input placeholder="最大坐席数" v-model="maxnum" />
              </li>
              <li>
                <span>最大登录并发数:</span>
                <a-input placeholder="最大登录并发数" v-model="maxcon" />
              </li>
            </ul>
          </div>
          <a-button type="primary" @click="handleAdd"> 添加 </a-button>
        </div>
      </div>
      <div>
        <ul>
          <li
            v-for="(item, index) in list"
            :key="index"
            @click="selectItem(item)"
            :class="{ active: selectedItem && selectedItem.id === item.id }"
          >
            <router-link :to="`/list-item-${item.id}`">
              <i :class="item.status == 1 ? 'i_greey' : 'i_red'"></i>&emsp;{{
                item.ename
              }}</router-link
            >
          </li>
        </ul>
      </div>
    </div>
    <div class="content">
      <div>
        <div>
          <div class="cont_main">
            <router-view @1="iou"></router-view>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getList, addList, Search } from "../api/EnterpriseManagement.js";
export default {
  data() {
    return {
      list: [],
      selectedItem: null,
      Add: false,
      currentTime: "",
      name: "",
      ip: "",
      admin: "",
      email: "",
      maxnum: "",
      maxcon: "",
      search: "",
      length: "",
      // i_greey: "",
      // i_red: "",
    };
  },

  created() {
    this.getlist();
  },
  mounted() {
    this.currentTime = this.getCurrentTime();
  },
  methods: {
    iou() {
      this.getlist()
    },
    //搜索
    Searchs() {
      Search({
        ename: this.search,
      }).then((res) => {
        if (res.msg == "超级企业不存在") {
          this.list = [];
          this.length = 0;
          return;
        }
        this.list = res.data.data;
        this.length = res.data.data.length;
      });
    },
    //根据名称搜索
    onChange(e) {
      console.log(e);
    },

    //获取当前时间
    getCurrentTime() {
      const date = new Date();
      const year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      let hour = date.getHours();
      let minute = date.getMinutes();
      let second = date.getSeconds();
      // 如果月份、日期、小时、分钟或秒小于10，前面补0
      month = month < 10 ? "0" + month : month;
      day = day < 10 ? "0" + day : day;
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;
      return `${year}-${month}-${day} ${hour}:${minute}:${second}`;
    },
    getlist() {
      getList().then((res) => {
        this.list = res.data.data;
        this.length = this.list.length;
        // this.statuss();
      });
    },
    selectItem(item) {
      this.selectedItem = item;
    },

    add() {
      if (this.Add == false) {
        this.Add = true;
      } else if (this.Add == true) {
        this.Add = false;
      }
    },
    addList() {
      addList({
        ename: this.name,
        ipstr: this.ip,
        adminname: this.admin,
        adminemil: this.email,
        maxnum: this.maxnum,
        maxconcurrency: this.maxcon,
        create_time: this.currentTime,
      }).then((res) => {
        if (res.code == 0) {
          this.$message.success("添加企业成功");
          this.Add = false;
        }else if(res.code!==0){
            this.$message.error(res.msg);
        }
      });
    },
    handleAdd() {
      this.getlist();
      this.addList();
    },

  },
};
</script>
<style lang="less" scoped>
.addEnter {
  width: 600px;
  height: 500px;
  position: absolute;
  top: 200px;
  left: 520px;
  background: white;
  z-index: 99;
  border: 1px solid rgb(241, 241, 241);
  padding: 10px;
  // 盒子阴影
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  h2 {
    border-bottom: 1px solid #eeeeee;
    button {
      float: right;
    }
    button:hover {
      color: red;
    }
  }
  > div {
    padding: 20px;
    ul {
      margin: 0;
      padding: 0;
      li {
        list-style: none;
        margin-bottom: 25px;
        span {
          display: inline-block;
          width: 160px;
          text-align: right;
        }
        input {
          display: inline-block;
          width: 60%;
        }
      }
    }
  }
  > button {
    display: block;
    margin: auto;
  }
}
.active {
  /* 这里定义点击后列表项的样式，例如改变背景色或字体颜色 */
  background-color: #f0f8ff; /* 浅蓝色背景 */
  color: #007bff; /* 深蓝色文字 */
}
.mains {
  display: flex;
  justify-content: space-between;
  background-color: #f0f0f0;
  .left {
    width: 20%;
    background: white;
    > div:first-child {
      width: 100%;
      height: 100px;
      background: white;
      padding: 10px;
      border-bottom: 1px solid rgb(241, 241, 241);
    }
    > div:last-child {
      width: 100%;
      height: 79vh;
      overflow: auto;
      ul {
        padding-left: 0px;
        li {
          padding-left: 20px;
          list-style: none;
          height: 50px;
          line-height: 50px;
          border-bottom: 1px solid rgb(241, 241, 241);
          a {
            display: block;
            width: 100%;
            height: 100%;
            i {
              display: inline-block;
              width: 14px;
              height: 14px;
              border-radius: 50%;
            }
          }
        }
      }
    }
  }
  .content {
    width: 78%;
    padding: 20px 20px 20px 0;

    > div {
      width: 100%;
      height: 100%;
      background: white;
      padding: 20px;
    }
  }
}
.i_greey {
  background: #38d88f;
}
.i_red {
  background: red;
}
</style>


