import { apiGet, apiPost } from "./api";
//获取企业管理列表信息
export function getList() {
  return new Promise((resolve) => {
    apiGet("/index.php/index/call/getsuperes").then((res) => {
      resolve(res);
    });
  });
}
export function getStr(id) {
  return new Promise((resolve) => {
    apiPost(`/index.php/index/call/getsuperbyid?id=${id}`).then((res) => {
      resolve(res);
    });
  });
}

//添加
export function addList(data) {
  return new Promise((resolve) => {
    apiGet("/index.php/index/call/addsuper", data).then((res) => {
      resolve(res);
    });
  });
}
//搜索
export function Search(name) {
  return new Promise((resolve) => {
    apiPost("/index.php/index/call/getsearches", name).then((res) => {
      resolve(res);
    });
  });
}

export function Status(id, num) {
  return new Promise((resolve) => {
    apiPost(`/index.php/index/call/upsuperstatus?id=${id}&status=${num}`).then(
      (res) => {
        resolve(res);
      }
    );
  });
}
